import React from 'react'
import styled from 'styled-components'
import tw from 'tailwind.macro'
import { Parallax } from 'react-spring/renderprops-addons.cjs'

// Components
import Layout from '../components/Layout'
import ProjectCard from '../components/ProjectCard'

// Elements
import Inner from '../elements/Inner'
import { Title, BigTitle, Subtitle } from '../elements/Titles'

// Views
import Hero from '../views/Hero'
import Projects from '../views/Projects'
import About from '../views/About'

const AboutDesc = styled.p`
  ${tw`text-grey-light text-lg md:text-xl lg:text-2xl font-sans pt-6 md:pt-12 text-justify`};
`

const ContactText = styled.p`
  ${tw`text-grey-light font-sans text-xl md:text-2xl lg:text-3xl`};
`

const Footer = styled.footer`
  ${tw`text-center text-grey absolute pin-b p-6 font-sans text-md lg:text-lg`};
`

const Index = () => (
  <>
    <Layout />
    <Parallax pages={5}>
      <About offset={0}>
        <Title>Stellar rotation and eMSTO</Title>
        <AboutDesc>
        Recent discoveries of extended main-sequence turn-offs (eMSTOs) and split main sequences in intermediate-age (around 1-3 Gyr-old) and young clusters (smaller than 400 Myr) pose a fundamental challenge to our traditional understanding of star clusters as prototypes of the canonical single stellar population. We used medium-resolution spectroscopic data to conduct a  census of stellar rotation rates of MS stars in various open clusters.
        </AboutDesc>
      </About>
    </Parallax>
  </>
)

export default Index
